import React, { useEffect, useState } from 'react';
import { Alert, Card, CardBody, Col, Row } from 'reactstrap';
import MaterialIcon from '@material/react-material-icon';
import General from './forms/General';
import Officials from './forms/Officials';
import Document from './forms/Document';
import Portfolio from './forms/Portfolio';
import BusinessField from './forms/BusinessField';
import './Step.scss';
import useAxios from '../../hooks/useAxios';

const Registration = () => {
  const [activeStep, setActiveStep] = useState(1);
  const [generalIsDone, setGeneralIsDone] = useState(false);
  const [officialIsDone, setOfficialIsDone] = useState(false);
  const [documentIsDone, setDocumentIsDone] = useState(false);
  const [portofolioIsDone, setPortofolioIsDone] = useState(false);
  const [businessFieldIsDone, setBusinessFieldIsDone] = useState(false);
  const [successMsg, setSuccessMsg] = useState();
  const [errMsg, setErrMsg] = useState();

  const api = useAxios();

  useEffect(() => {
    async function fetchDocumentStatus() {
      await api
        .get('vapi/perusahaan/document-status')
        .then((res) => setDocumentIsDone(res.data.data));
    }

    async function fetchJajaranStatus() {
      await api
        .get('vapi/perusahaan/jajaran-status')
        .then((res) => setOfficialIsDone(res.data.data));
    }

    async function fetchPortoStatus() {
      await api
        .get('vapi/perusahaan/portofolio-status')
        .then((res) => setPortofolioIsDone(res.data.data));
    }

    async function fetchBusinessFieldStatus() {
      await api
        .get('vapi/perusahaan/bidangusaha-status')
        .then((res) => setBusinessFieldIsDone(res.data.data));
    }

    fetchDocumentStatus();
    fetchJajaranStatus();
    fetchPortoStatus();
    fetchBusinessFieldStatus();
  }, []);

  const steps = [
    {
      id: 1,
      title: 'Data Umum Perusahaan',
      isDone: generalIsDone,
    },
    {
      id: 2,
      title: 'Jajaran Direksi/Komisaris Perusahaan',
      isDone: officialIsDone,
    },
    {
      id: 3,
      title: 'Dokumen Perusahaan',
      isDone: documentIsDone,
    },
    {
      id: 4,
      title: 'Portofolio Pengalaman',
      isDone: portofolioIsDone,
    },
    {
      id: 5,
      title: 'Daftar Bidang Usaha',
      isDone: businessFieldIsDone,
    },
  ];

  useEffect(() => {
    if (successMsg) {
      setTimeout(() => {
        setSuccessMsg();
      }, 5000);
    }

    if (errMsg) {
      setTimeout(() => {
        setErrMsg();
      }, 5000);
    }
  }, [successMsg, errMsg]);

  return (
    <>
      <Row lg="12" className="justify-content-center">
        <Col lg="12">
          <Card className="rounded-4 w-100">
            <CardBody>
              <div className="step-container">
                {steps.map((item) => (
                  <div
                    key={item.id}
                    className={`step-item ${item.isDone && 'done hover'}`}
                    onClick={() => (item.isDone ? setActiveStep(item.id) : '')}
                  >
                    <div
                      className={`step-title ${
                        activeStep === item.id ? 'text-dark' : 'text-muted'
                      }`}
                    >
                      {item.title}
                    </div>
                    <div className={`step-circle ${item.isDone && 'done'}`}>
                      <MaterialIcon
                        icon={`${
                          item.isDone ? 'check' : item.id === activeStep ? 'update' : 'adjust'
                        }`}
                      />
                    </div>
                  </div>
                ))}
              </div>
            </CardBody>
          </Card>
        </Col>
      </Row>
      {successMsg && (
        <Alert
          className="rounded-4 d-flex justify-content-between text-success fw-bold"
          color="success"
        >
          {successMsg}
          <MaterialIcon icon="close" onClick={() => setSuccessMsg(!successMsg)} />
        </Alert>
      )}
      {errMsg && (
        <Alert
          className="rounded-4 d-flex justify-content-between text-danger fw-bold"
          color="danger"
        >
          {errMsg}
          <MaterialIcon icon="close" onClick={() => setErrMsg(!errMsg)} />
        </Alert>
      )}
      <div>
        {activeStep === 1 && <General {...{ setActiveStep, generalIsDone, setGeneralIsDone }} />}
        {activeStep === 2 && (
          <Officials {...{ setActiveStep, officialIsDone, setOfficialIsDone }} />
        )}
        {activeStep === 3 && (
          <Document {...{ setActiveStep, setSuccessMsg, setErrMsg, documentIsDone }} />
        )}
        {activeStep === 4 && (
          <Portfolio
            {...{ setActiveStep, portofolioIsDone, setPortofolioIsDone, setSuccessMsg, setErrMsg }}
          />
        )}
        {activeStep === 5 && (
          <BusinessField
            {...{
              setActiveStep,
              businessFieldIsDone,
              setBusinessFieldIsDone,
              setSuccessMsg,
              setErrMsg,
            }}
          />
        )}
      </div>
    </>
  );
};

export default Registration;
