import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import { Button, Col, Form, FormGroup, Input, Label, Row, Spinner, Table } from 'reactstrap';
import { useQuery } from '@tanstack/react-query';
import { Link } from 'react-router-dom';
import MaterialIcon from '@material/react-material-icon';
import getBase64 from '../../../../utils/generateFile';
import useAxios from '../../../../hooks/useAxios';
import { alert } from '../../../../components/toast/Toast';

const IzinBerusaha = ({ setSuccessMsg, setErrMsg }) => {
  const [datas, setDatas] = useState();
  const [file, setFile] = useState();
  const [isSending, setIsSending] = useState(false);
  const [selectedId, setSelectedId] = useState(false);
  const [isDeleting, setIsDeleting] = useState(false);
  const [isOpenForm, setIsOpenForm] = useState(false);
  const [izinLainnya, setIzinLainnya] = useState(false);

  const api = useAxios();

  const { isLoading, error, data, refetch } = useQuery({
    queryKey: ['izin'],
    queryFn: () =>
      api.get(`vapi/izin/my`).then((res) => {
        return res.data.data;
      }),
  });

  console.log('ahai', izinLainnya);

  useEffect(() => {
    if (data?.length === 0) {
      setIsOpenForm(true);
    }
  }, [data]);

  const handleDoc = (e) => {
    getBase64(e.target.files[0])
      .then((res) => setFile(res.split(',')[1]))
      .catch((err) => console.log(err));
  };

  const handleChange = (e) => {
    setDatas((prev) => ({ ...prev, [e.target.id]: e.target.value }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setIsSending(true);
    datas.file = file;
    datas.keterangan = izinLainnya ? 'Izin Lainnya' : '';
    await api
      .post('vapi/izin/store', datas)
      .then(() => {
        refetch();
        alert('success', 'Simpan data berhasil');
        setSuccessMsg('Data Izin Berusaha Perusahaan berhasil disimpan.');
      })
      .catch(() => {
        alert('error', 'Gagal menyimpan data');
        setErrMsg('Gagal menyimpan data. Silakan coba lagi.');
      });
    setIsOpenForm(false);
    setIsSending(false);
  };

  const handleDelete = async (id) => {
    setSelectedId(id);
    setIsDeleting(true);
    await api
      .get(`vapi/izin/delete/${id}`)
      .then(() => {
        refetch();
      })
      .catch((err) => console.log(err));
    setIsDeleting(false);
  };

  return (
    <>
      <h5>Izin Berusaha</h5>
      {isLoading
        ? 'Loading..'
        : error
        ? 'Something went wrong.'
        : data.length > 0 && (
            <>
              <Table hover responsive bordered>
                <thead>
                  <tr>
                    <th>Nomor</th>
                    <th>Nama Izin</th>
                    <th>Tanggal Terbit</th>
                    <th>Tanggal Berakhir</th>
                    <th>Ket</th>
                    <th>File</th>
                    <th style={{ textAlign: 'right' }}>Action</th>
                  </tr>
                </thead>
                <tbody>
                  {data.map((iz) => (
                    <tr key={iz.id_izin}>
                      <td>{iz.nomor}</td>
                      <td>{iz.nama_izin}</td>
                      <td>{iz.tgl_terbit}</td>
                      <td>{iz.tgl_berakhir ? iz.tgl_berakhir : '-'}</td>
                      <td>{iz.keterangan ? iz.keterangan : '-'}</td>
                      <td>
                        <abbr title="Download file">
                          <Link to="">
                            <MaterialIcon icon="download" />
                          </Link>
                        </abbr>
                      </td>
                      <td style={{ textAlign: 'right', width: '100px' }}>
                        {isDeleting && selectedId === iz.id_izin ? (
                          <span className="text-danger">Deleting..</span>
                        ) : (
                          <MaterialIcon
                            icon="delete"
                            style={{ fontSize: '16px', cursor: 'pointer', color: 'red' }}
                            onClick={() => handleDelete(iz.id_izin)}
                          />
                        )}
                      </td>
                    </tr>
                  ))}
                </tbody>
              </Table>
              <div className="d-flex justify-content-end mt-2">
                {isOpenForm ? (
                  <Button
                    type="button"
                    color="dark"
                    size="sm"
                    outline
                    onClick={() => setIsOpenForm(false)}
                    content="center"
                  >
                    Tutup
                  </Button>
                ) : (
                  <Button
                    className="mb-4"
                    type="button"
                    color="dark"
                    size="sm"
                    outline
                    onClick={() => setIsOpenForm(true)}
                  >
                    Tambah Izin
                  </Button>
                )}
              </div>
            </>
          )}
      {isOpenForm && (
        <Form onSubmit={handleSubmit}>
          <Col className="bg-light-primary rounded-4 px-3 pt-3 pb-1 mt-3 mb-3">
            <Row>
              <Col sm="12" lg="4">
                <FormGroup check>
                  <Input
                    type="checkbox"
                    id="dok_surat_penyampaian_penawaran"
                    onChange={(e) => setIzinLainnya(e.target.checked)}
                  />
                  <Label check htmlFor="dok_surat_penyampaian_penawaran" className="form-label">
                    Izin Usaha Lainnya (Jika Ada)
                  </Label>
                </FormGroup>
              </Col>
            </Row>
            <Row>
              <Col sm="12" lg="4">
                <FormGroup>
                  <Label htmlFor="nomor">Nomor Izin Berusaha</Label>
                  <Input
                    type="text"
                    id="nomor"
                    name="nomor"
                    placeholder="Nomor"
                    onChange={(e) => handleChange(e)}
                  />
                </FormGroup>
              </Col>
              <Col sm="12" lg="4">
                <FormGroup>
                  <Label htmlFor="tgl_terbit">Tanggal Dikeluarkan</Label>
                  <Input
                    type="date"
                    id="tgl_terbit"
                    name="tgl_terbit"
                    placeholder="Tanggal dikeluarkan"
                    onChange={(e) => handleChange(e)}
                    required
                  />
                </FormGroup>
              </Col>
              {izinLainnya ? (
                <Col sm="12" lg="4">
                  <FormGroup className="position-relative">
                    <Label htmlFor="tgl_berakhir">Nama Izin Lainnya</Label>
                    <Input
                      type="text"
                      id="nama_izin"
                      name="nama_izin"
                      onChange={(e) => handleChange(e)}
                    />
                  </FormGroup>
                </Col>
              ) : (
                <Col sm="12" lg="4">
                  <FormGroup>
                    <Label htmlFor="tgl_berakhir">Berakhir</Label>
                    <Input
                      type="date"
                      id="tgl_berakhir"
                      name="tgl_berakhir"
                      placeholder="Tanggal Berakhir"
                      onChange={(e) => handleChange(e)}
                      required
                    />
                  </FormGroup>
                </Col>
              )}
            </Row>
            <Row className="mb-4">
              <Col sm="12" lg="4">
                <FormGroup className="position-relative">
                  <Input
                    type="file"
                    id="file"
                    name="file"
                    onChange={(e) => handleDoc(e)}
                    required
                    accept="application/pdf"
                  />
                  <small className="text-muted position-absolute">Upload file (max. 10mb)</small>
                </FormGroup>
              </Col>
              <Col sm="12" lg="4">
                <Button type="submit" color="secondary">
                  {isSending ? (
                    <div className="d-flex align-items-center gap-2">
                      <Spinner size="sm" />
                      Menyimpan..
                    </div>
                  ) : (
                    'Simpan'
                  )}
                </Button>
              </Col>
            </Row>
          </Col>
        </Form>
      )}
    </>
  );
};

IzinBerusaha.propTypes = {
  setSuccessMsg: PropTypes.func,
  setErrMsg: PropTypes.func,
};

export default IzinBerusaha;
