import React from 'react';
import './FileView.scss';
import { Col, Row } from 'reactstrap';
import MaterialIcon from '@material/react-material-icon';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';

const FileView = ({ title, filename, children }) => {
  return (
    <Row className="mb-4">
      <span>{title}</span>
      <Col sm="12" lg="12" className="file-view">
        <div className="d-flex rounded-2 border align-items-center justify-content-between overflow-hidden">
          <div className="h-100 d-flex p-3 bg-success">
            <MaterialIcon icon="text_snippet" style={{ color: 'white' }} />
          </div>
          <div className="d-flex w-100 px-3 fw-bold">
            <Link>{filename}</Link>
          </div>
          <div className="d-flex p-2 ">{children}</div>
        </div>
      </Col>
      {/* <Col sm="12" lg="1">
        <Button type="button" block outline>
          Edit
        </Button>
      </Col> */}
    </Row>
  );
};

FileView.propTypes = {
  title: PropTypes.string,
  filename: PropTypes.string,
  children: PropTypes.element,
};

export default FileView;
