import React from 'react';
import PropTypes from 'prop-types';
import { Button, Card, CardBody, Col, FormGroup, Row } from 'reactstrap';
// import { Link } from 'react-router-dom';
import AktaPendirian from './docforms/AktaPendirian';
import IzinBerusaha from './docforms/IzinBerusaha';
import Lainnya from './docforms/Lainnya';

const Document = ({ setActiveStep, setSuccessMsg, setErrMsg, documentIsDone }) => {
  return (
    <>
      <Row lg="12" className="justify-content-center">
        <Col lg="12">
          <Card className="rounded-4 w-100">
            <CardBody>
              <h4 className="fw-bold">Dokumen Perusahaan</h4>
              <AktaPendirian {...{ setSuccessMsg, setErrMsg }} />
              <IzinBerusaha {...{ setSuccessMsg, setErrMsg }} />
              <Lainnya {...{ setSuccessMsg, setErrMsg }} />
              <FormGroup className="d-flex justify-content-end mt-4">
                <Button
                  type="button"
                  color="primary"
                  className="me-2"
                  outline
                  onClick={() => setActiveStep(2)}
                >
                  Sebelumnya
                </Button>
                <Button
                  type="button"
                  color="primary"
                  className="me-2"
                  disabled={!documentIsDone}
                  onClick={() => setActiveStep(4)}
                >
                  Selanjutnya
                </Button>
              </FormGroup>
            </CardBody>
          </Card>
        </Col>
      </Row>
    </>
  );
};

Document.propTypes = {
  setActiveStep: PropTypes.func,
  setSuccessMsg: PropTypes.func,
  setErrMsg: PropTypes.func,
  documentIsDone: PropTypes.bool,
};

export default Document;
