import MaterialIcon from '@material/react-material-icon';
import React, { useState } from 'react';
import { Button, FormGroup, Spinner } from 'reactstrap';
import PropTypes from 'prop-types';
import useAxios from '../../../hooks/useAxios';
import { alert } from '../../../components/toast/Toast';

const SubmitButton = ({ setActiveStep, businessFieldIsDone, setSuccessMsg, setErrMsg }) => {
  const [isSending, setIsSending] = useState();

  const api = useAxios();

  const handleSubmit = async () => {
    setIsSending(true);
    await api
      .put('vapi/perusahaan/submit')
      .then(() => {
        alert('success', 'Data berhasil dikirim');
        setSuccessMsg(
          'Data Perusahaan Anda akan direview oleh Admin. Silakan cek email secara berkala untuk pembaruan status selanjutnya.',
        );
      })
      .catch(() => {
        setErrMsg('Gagal mengirim data. Silakan coba lagi.');
      });
    setIsSending(false);
  };

  console.log(businessFieldIsDone);

  return (
    <FormGroup className="d-flex justify-content-end mt-4">
      <Button
        type="button"
        color="primary"
        className="me-2"
        outline
        onClick={() => setActiveStep(4)}
      >
        Sebelumnya
      </Button>
      {isSending ? (
        <Button type="button" color="primary" disabled>
          <div className="d-flex align-items-center gap-2">
            <Spinner size="sm" />
            Mengirim..
          </div>
        </Button>
      ) : (
        <Button
          type="button"
          color="success"
          className="me-2 d-flex gap-2"
          onClick={handleSubmit}
          disabled={!businessFieldIsDone}
        >
          <MaterialIcon icon="send" />
          Kirim
        </Button>
      )}
    </FormGroup>
  );
};

SubmitButton.propTypes = {
  setActiveStep: PropTypes.func,
  businessFieldIsDone: PropTypes.bool,
  setSuccessMsg: PropTypes.func,
  setErrMsg: PropTypes.func,
};

export default SubmitButton;
