import React, { useEffect, useState } from 'react';
import { Button, Card, CardBody, Col, Row, Form, Table, Spinner } from 'reactstrap';
import Select from 'react-select';
import PropTypes from 'prop-types';
import MaterialIcon from '@material/react-material-icon';
import { useQuery } from '@tanstack/react-query';
import useAxios from '../../../hooks/useAxios';
import SubmitButton from './SubmitButton';
import { alert } from '../../../components/toast/Toast';

const BusinessField = ({
  setActiveStep,
  businessFieldIsDone,
  setBusinessFieldIsDone,
  setSuccessMsg,
  setErrMsg,
}) => {
  const [isOpenForm, setIsOpenForm] = useState();
  const [isSending, setIsSending] = useState(false);
  const [isDeleting, setIsDeleting] = useState(false);
  const [values, setValues] = useState();
  const [kbli, setKbli] = useState();
  // const [kbliList, setKbliList] = useState([]);
  const [selectedId, setSelectedId] = useState();

  const api = useAxios();

  const { isLoading, error, data, refetch } = useQuery({
    queryKey: ['listkbli'],
    queryFn: () =>
      api.get(`vapi/kbli/view`).then((res) => {
        if (res.data.data?.length > 0) {
          setBusinessFieldIsDone(true);
        } else {
          setBusinessFieldIsDone(false);
        }
        return res.data.data;
      }),
  });

  useEffect(() => {
    async function fetchListKbli() {
      await api
        .get('vapi/kbli/list')
        .then((res) => setKbli(res.data.data))
        .catch((err) => console.log(err));
    }

    fetchListKbli();
  }, []);

  const handleSubmit = async (e) => {
    e.preventDefault();
    setIsSending(true);
    if (values) {
      await api
        .post('vapi/kbli/store', { id_kbli: values.value })
        .then(() => {
          refetch();
          alert('success', 'KBLI berhasil ditambah');
        })
        .catch((err) => {
          if (err.response.status === 409) {
            alert('error', 'Data KBLI sudah ada.');
          } else {
            alert('Gagal menyimpan data. Silakan coba lagi.');
          }
        });
    } else {
      alert('error', 'Tidak ada KBLI yang dipilih');
    }
    setIsOpenForm(false);
    setIsSending(false);
  };

  const handleDelete = async (id) => {
    setSelectedId(id);
    setIsDeleting(true);
    await api
      .delete(`vapi/kbli/delete/${id}`)
      .then(() => {
        refetch();
        alert('success', 'KBLI berhasil dihapus.');
      })
      .catch(() => {
        alert('error', 'Gagal menghapus data. Silakan coba lagi.');
      });
    setIsDeleting(false);
  };

  return (
    <Row lg="12" className="justify-content-center">
      <Col lg="12">
        <Card className="rounded-4 w-100">
          <CardBody>
            <h4 className="fw-bold">Daftar Bidang Usaha</h4>
            {isLoading ? (
              'Loading..'
            ) : error ? (
              'Something went wrong.'
            ) : (
              <Table hover>
                {data?.length > 0 ? (
                  <tbody>
                    {data.map((k) => (
                      <tr key={k.id}>
                        <td style={{ textAlign: 'left' }}>NIB</td>
                        <td style={{ textAlign: 'left' }}>{k.nomor_kbli}</td>
                        <td style={{ textAlign: 'left' }}>{k.nama_kbli}</td>
                        <td style={{ textAlign: 'left' }}>{k.resiko}</td>
                        <td style={{ textAlign: 'right', width: '100px' }}>
                          {isDeleting && selectedId === k.id ? (
                            <span className="text-danger">Deleting..</span>
                          ) : (
                            <MaterialIcon
                              icon="delete"
                              style={{ fontSize: '16px', cursor: 'pointer', color: 'red' }}
                              onClick={() => handleDelete(k.id)}
                            />
                          )}
                        </td>
                      </tr>
                    ))}
                  </tbody>
                ) : (
                  <tbody>
                    <tr>
                      <td>Belum ada data.</td>
                    </tr>
                  </tbody>
                )}
              </Table>
            )}
            <div className="d-flex justify-content-end mt-2">
              {!isOpenForm && (
                <Button
                  type="button"
                  color="dark"
                  size="sm"
                  outline
                  onClick={() => setIsOpenForm(true)}
                >
                  Tambah KBLI
                </Button>
              )}
            </div>
            {isOpenForm && (
              <Form onSubmit={handleSubmit}>
                <Col className="bg-light-primary rounded-4 px-3 pt-3 mt-3">
                  <Row>
                    <Col sm="12" lg="8" className="mb-3">
                      <Select
                        closeMenuOnSelect
                        options={kbli}
                        onChange={(choice) => setValues(choice)}
                      />
                      {/* <FormGroup>
                        <Input
                          type="select"
                          id="id_kbli"
                          name="id_kbli"
                          onChange={(e) => setValues({ id_kbli: e.target.value })}
                          defaultValue=""
                        >
                          {kbli?.length > 0 ? (
                            <>
                              <option value="" disabled>
                                - Pilih -
                              </option>
                              {kbli?.map((k) => (
                                <option key={k.id_kbli} value={k.id_kbli}>
                                  {k.nomor_kbli} - {k.nama_kbli}
                                </option>
                              ))}
                            </>
                          ) : (
                            <option>Loading..</option>
                          )}
                        </Input>
                      </FormGroup> */}
                    </Col>
                    <Col sm="12" lg="2">
                      <div className="d-flex gap-2">
                        <Button color="secondary" outline onClick={() => setIsOpenForm(false)}>
                          Cancel
                        </Button>
                        <Button type="submit" color="dark">
                          {isSending ? (
                            <div className="d-flex align-items-center gap-2">
                              <Spinner size="sm" />
                              Menyimpan..
                            </div>
                          ) : (
                            'Tambah'
                          )}
                        </Button>
                      </div>
                    </Col>
                  </Row>
                </Col>
              </Form>
            )}
            <SubmitButton {...{ setActiveStep, businessFieldIsDone, setSuccessMsg, setErrMsg }} />
          </CardBody>
        </Card>
      </Col>
    </Row>
  );
};

BusinessField.propTypes = {
  setActiveStep: PropTypes.func,
  businessFieldIsDone: PropTypes.bool,
  setBusinessFieldIsDone: PropTypes.func,
  setSuccessMsg: PropTypes.func,
  setErrMsg: PropTypes.func,
};

export default BusinessField;
