import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import {
  Button,
  Card,
  CardBody,
  Col,
  FormGroup,
  Input,
  Label,
  Row,
  Spinner,
  Form,
  Alert,
} from 'reactstrap';
import InputMask from 'react-input-mask';
import { Link } from 'react-router-dom';
import { useQuery } from '@tanstack/react-query';
import MaterialIcon from '@material/react-material-icon';
import useAxios from '../../../hooks/useAxios';
import { alert } from '../../../components/toast/Toast';

const General = ({ setActiveStep, generalIsDone, setGeneralIsDone }) => {
  const [bidangUsaha, setBidangUsaha] = useState();
  const [isSending, setIsSending] = useState(false);
  const [dataTemp, SetDataTemp] = useState({});
  const [errMsg, setErrMsg] = useState('');
  const [filePvd, setFilePvd] = useState();
  const [fileNpwp, setFileNpwp] = useState();
  const [listProv, setListProv] = useState([]);
  const [fieldPvdIsOpen, setFieldPvdIsOpen] = useState(false);
  const [fieldNpwpIsOpen, setFieldNpwpIsOpen] = useState(false);

  const api = useAxios();

  const API_URL = process.env.REACT_APP_BASEURL;

  const { isLoading, error, data } = useQuery({
    queryKey: ['general'],
    queryFn: () =>
      api.get(`vapi/perusahaan/data-umum`).then((res) => {
        if (res.data.data?.email_alternatif !== null) {
          setGeneralIsDone(true);
        }
        return res.data.data;
      }),
  });

  useEffect(() => {
    async function loadNames() {
      await fetch('https://api.goapi.io/regional/provinsi?api_key=fb5373cf-3862-580f-a71c-f2aadaf7')
        .then((res) => res.json())
        .then((provs) => setListProv(provs.data))
        .catch((err) => console.log(err));
    }

    loadNames();
  }, []);

  useEffect(() => {
    SetDataTemp(data);
    setFilePvd(data?.file_pvd);
    setFileNpwp(data?.file_npwp);
  }, [data]);

  useEffect(() => {
    async function fetchListBidangUsaha() {
      await api
        .get('vapi/perusahaan/list-bidang-usaha')
        .then((res) => {
          setBidangUsaha(res.data.data);
        })
        .catch((err) => console.log(err));
    }

    fetchListBidangUsaha();
  }, []);

  // const initialValues = {
  //   nama_perusahaan: '',
  //   tipePenyedia: '',
  //   bentukPerusahaan: '',
  //   email: '',
  //   npwp: '',
  //   bidangUsaha: '',
  //   kontak: '',
  //   alamat: '',
  //   provinsi: '',
  //   filePvd: '',
  //   fileNpwp: '',
  // };

  // const validationSchema = Yup.object().shape({
  //   nama_perusahaan: Yup.string().required('Nama perusahaan harus diisi.'),
  //   tipePenyedia: Yup.string().required('Tipe penyedia harus diisi.'),
  //   bentukPerusahaan: Yup.string().required('Bentuk perusahaan harus diisi.'),
  //   email: Yup.string().email('Email tidak valid.').required('Email harus diisi.'),
  //   npwp: Yup.string().required('NPWP harus diisi.'),
  //   bidangUsaha: Yup.string().required('Bidang usaha harus diisi.'),
  //   kontak: Yup.number().required('Kontak harus diisi.'),
  //   alamat: Yup.string().required('Alamat perusahaan harus diisi.'),
  //   provinsi: Yup.string().required('Provinsi harus diisi.'),
  //   filePvd: Yup.string().required('File PVD harus diisi.'),
  //   fileNpwp: Yup.string().required('File NPWP harus diisi.'),
  // });

  const handleChange = (e) => {
    SetDataTemp((prev) => ({ ...prev, [e.target.id]: e.target.value }));
  };

  const getBase64 = (file) => {
    return new Promise((resolve) => {
      let baseURL = '';
      const reader = new FileReader();
      reader.readAsDataURL(file);
      reader.onload = () => {
        baseURL = reader.result;
        resolve(baseURL);
      };
    });
  };

  const handleFilePvd = (e) => {
    getBase64(e.target.files[0])
      .then((result) => {
        setFilePvd(result.split(',')[1]);
      })
      .catch((err) => {
        console.log(err);
      });
  };

  const handleFileNpwp = (e) => {
    getBase64(e.target.files[0])
      .then((result) => {
        setFileNpwp(result.split(',')[1]);
      })
      .catch((err) => {
        console.log(err);
      });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setIsSending(true);

    dataTemp.filePvd = filePvd;
    dataTemp.fileNpwp = fileNpwp;

    await api
      .post('vapi/perusahaan/store', dataTemp)
      .then(() => {
        setGeneralIsDone(true);
        alert('success', 'Data umum perusahaan berhasil disimpan.');
        setActiveStep(2);
      })
      .catch((err) => {
        console.log(err);
        alert('error', 'Something went wrong.');
        setErrMsg(err.response.data.message);
      });
    setIsSending(false);
  };

  console.log(dataTemp);

  return (
    <>
      {errMsg && (
        <Alert className="rounded-4 d-flex justify-content-between" color="danger">
          {errMsg}
          <MaterialIcon icon="close" onClick={() => setErrMsg(!errMsg)} />
        </Alert>
      )}
      <Row lg="12" className="justify-content-center">
        <Col lg="12">
          <Card className="rounded-4 w-100">
            {isLoading ? (
              <CardBody>Loading...</CardBody>
            ) : error ? (
              <CardBody>Something went wrong.</CardBody>
            ) : (
              <CardBody>
                <h4 className="fw-bold">Data Umum Perusahaan</h4>
                <Form onSubmit={handleSubmit}>
                  <Row>
                    <Col sm="12" lg="7">
                      <FormGroup>
                        <Label htmlFor="nama_perusahaan">Nama Perusahaan</Label>
                        <Input
                          type="text"
                          id="nama_perusahaan"
                          name="nama_perusahaan"
                          value={dataTemp?.nama_perusahaan || ''}
                          className="form-control"
                          onChange={(e) => handleChange(e)}
                        />
                      </FormGroup>
                    </Col>
                    <Col sm="12" lg="5">
                      <FormGroup>
                        <Label htmlFor="uniqueId">Unique ID</Label>
                        <Input
                          type="text"
                          id="uniqueId"
                          name="uniqueId"
                          placeholder="Auto Generated"
                          value={dataTemp?.nomor_registrasi || ''}
                          disabled
                          onChange={(e) => handleChange(e)}
                        />
                      </FormGroup>
                    </Col>
                  </Row>
                  <Row>
                    <Col sm="12" lg="7">
                      <FormGroup>
                        <Label htmlFor="tipePenyedia">Bidang Usaha</Label>
                        <Input
                          type="select"
                          id="tipePenyedia"
                          name="tipePenyedia"
                          className="form-control"
                          value={dataTemp?.tipe || ''}
                          onChange={(e) => handleChange(e)}
                        >
                          <option value="" disabled>
                            - Pilih -
                          </option>
                          <option value="Barang">Barang</option>
                          <option value="Jasa">Jasa</option>
                          <option value="Barang dan Jasa">Barang dan Jasa</option>
                        </Input>
                      </FormGroup>
                    </Col>
                    <Col sm="12" lg="5">
                      <FormGroup>
                        <Label htmlFor="bentukPerusahaan">Bentuk Perusahaan</Label>
                        <Input
                          type="select"
                          id="bentukPerusahaan"
                          name="bentukPerusahaan"
                          className="form-control"
                          value={data?.bentuk_usaha || ''}
                          onChange={(e) => handleChange(e)}
                        >
                          <option value="" disabled>
                            - Pilih -
                          </option>
                          <option value="PT">PT</option>
                          <option value="CV">CV</option>
                          <option value="Firma">Firma</option>
                        </Input>
                      </FormGroup>
                    </Col>
                  </Row>
                  <Row>
                    <Col sm="12" lg="7">
                      <FormGroup>
                        <Label htmlFor="email_alternatif">Email Perusahaan</Label>
                        <Input
                          type="email_alternatif"
                          id="email_alternatif"
                          name="email_alternatif"
                          className="form-control"
                          value={dataTemp?.email_alternatif || ''}
                          onChange={handleChange}
                        />
                      </FormGroup>
                    </Col>
                    <Col sm="12" lg="5">
                      <FormGroup>
                        <Label htmlFor="no_npwp">NPWP</Label>
                        <InputMask
                          mask="99.999.999.9-999.999"
                          type="text"
                          id="no_npwp"
                          name="no_npwp"
                          className="form-control"
                          value={dataTemp?.no_npwp || ''}
                          onChange={handleChange}
                        />
                      </FormGroup>
                    </Col>
                  </Row>
                  <Row>
                    <Col sm="12" lg="7">
                      <FormGroup>
                        <Label htmlFor="id_bidang">Bidang Usaha</Label>
                        <Input
                          type="select"
                          id="id_bidang"
                          name="id_bidang"
                          className="form-control"
                          value={dataTemp?.id_bidang || ''}
                          onChange={handleChange}
                        >
                          <option value="">- Pilih -</option>
                          {bidangUsaha?.map((item) => (
                            <option key={item.id_bidang} value={item.id_bidang}>
                              {item.nama_bidang}
                            </option>
                          ))}
                        </Input>
                      </FormGroup>
                    </Col>
                    <Col sm="12" lg="5">
                      <FormGroup>
                        <Label htmlFor="hp">Kontak (Aktif WhatsApp)</Label>
                        <InputMask
                          className="form-control"
                          mask="+62\   999 9999 9999"
                          type="text"
                          id="hp"
                          name="hp"
                          value={dataTemp?.hp || ''}
                          onChange={handleChange}
                        />
                      </FormGroup>
                    </Col>
                  </Row>
                  <Row>
                    <Col sm="12" lg="7">
                      <FormGroup>
                        <Label htmlFor="alamat">Alamat Perusahaan</Label>
                        <Input
                          type="textarea"
                          id="alamat"
                          name="alamat"
                          className="form-control"
                          value={dataTemp?.alamat || ''}
                          onChange={handleChange}
                        />
                      </FormGroup>
                    </Col>
                    <Col sm="12" lg="5">
                      <FormGroup>
                        <Label htmlFor="provinsi">Provinsi</Label>
                        <Input
                          type="select"
                          id="provinsi"
                          name="provinsi"
                          className="form-control"
                          value={dataTemp?.provinsi || ''}
                          onChange={handleChange}
                        >
                          <option value="" disabled>
                            - Pilih -
                          </option>
                          {listProv?.map((prov) => (
                            <option value={prov.name} key={prov.id}>
                              {prov.name}
                            </option>
                          ))}
                        </Input>
                      </FormGroup>
                    </Col>
                  </Row>
                  <Row>
                    <Col sm="12" lg="7">
                      <FormGroup>
                        <Label htmlFor="filePvd">Surat penunjukkan Penanggungjawab Akun PVD</Label>
                        {data?.file_pvd === '-' ? (
                          <>
                            <Input
                              type="file"
                              id="filePvd"
                              name="filePvd"
                              className="form-control"
                              accept="application/pdf"
                              onChange={(e) => handleFilePvd(e)}
                            />
                            <div className="d-flex justify-content-between">
                              <small className="text-muted">Upload file (max. 10mb)</small>
                              <small>
                                <i>
                                  <Link
                                    to={`${API_URL}vendor_file/templates/pvd.docx`}
                                    className="text-decoration-none"
                                    target="_blank"
                                  >
                                    *Download template surat
                                  </Link>
                                </i>
                              </small>
                            </div>
                          </>
                        ) : (
                          <Row>
                            <Col md="11">
                              {fieldPvdIsOpen ? (
                                <>
                                  <Input
                                    type="file"
                                    id="filePvd"
                                    name="filePvd"
                                    className="form-control"
                                    accept="application/pdf"
                                    onChange={(e) => handleFilePvd(e)}
                                  />
                                  <div className="d-flex justify-content-between">
                                    <small className="text-muted">Upload file (max. 10mb)</small>
                                    <small>
                                      <i>
                                        <Link
                                          to={`${API_URL}vendor_file/templates/pvd.docx`}
                                          className="text-decoration-none"
                                          target="_blank"
                                        >
                                          *Download template surat
                                        </Link>
                                      </i>
                                    </small>
                                  </div>
                                </>
                              ) : (
                                <Link
                                  to={`/preview?data=general&doc=${dataTemp?.pvd_base64}`}
                                  target="_blank"
                                >
                                  <Button type="button" size="sm" color="light">
                                    Preview File PVD
                                  </Button>
                                </Link>
                              )}
                            </Col>
                            <Col md="1">
                              <button type="button" style={{ border: 'none', background: 'none' }}>
                                <MaterialIcon
                                  icon={`${fieldPvdIsOpen ? 'close' : 'edit'}`}
                                  onClick={() => {
                                    setFieldPvdIsOpen(!fieldPvdIsOpen);
                                  }}
                                  className="text-muted"
                                  style={{ fontSize: '20px' }}
                                />
                              </button>
                            </Col>
                          </Row>
                        )}
                      </FormGroup>
                    </Col>
                    <Col sm="12" lg="5">
                      <FormGroup>
                        <Label htmlFor="fileNpwp">Upload NPWP </Label>
                        {data?.file_npwp === '-' ? (
                          <>
                            <Input
                              type="file"
                              id="filePvd"
                              name="filePvd"
                              className="form-control"
                              accept="application/pdf"
                              onChange={(e) => handleFilePvd(e)}
                            />
                            <div className="d-flex justify-content-between">
                              <small className="text-muted">Upload file (max. 10mb)</small>
                            </div>
                          </>
                        ) : (
                          <Row>
                            <Col md="10">
                              {fieldNpwpIsOpen ? (
                                <>
                                  <Input
                                    type="file"
                                    id="fileNpwp"
                                    name="fileNpwp"
                                    className="form-control"
                                    onChange={(e) => handleFileNpwp(e)}
                                    accept="application/pdf"
                                  />
                                  <div className="d-flex justify-content-between">
                                    <small className="text-muted">Upload file (max. 10mb)</small>
                                  </div>
                                </>
                              ) : (
                                <Link
                                  to={`/preview?data=general&doc=${dataTemp?.npwp_base64}`}
                                  target="_blank"
                                >
                                  <Button type="button" size="sm" color="light">
                                    Preview File NPWP
                                  </Button>
                                </Link>
                              )}
                            </Col>
                            <Col md="1">
                              <button type="button" style={{ border: 'none', background: 'none' }}>
                                <MaterialIcon
                                  icon={`${fieldNpwpIsOpen ? 'close' : 'edit'}`}
                                  onClick={() => {
                                    setFieldNpwpIsOpen(!fieldNpwpIsOpen);
                                  }}
                                  className="text-muted"
                                  style={{ fontSize: '20px' }}
                                />
                              </button>
                            </Col>
                          </Row>
                        )}
                      </FormGroup>
                    </Col>
                  </Row>
                  <FormGroup className="d-flex justify-content-end">
                    {generalIsDone ? (
                      <>
                        <Button
                          type="submit"
                          color="secondary"
                          className="me-2"
                          aria-expanded="false"
                          outline
                        >
                          {isSending ? (
                            <div className="d-flex align-items-center gap-2">
                              <Spinner size="sm" />
                              Mengirim..
                            </div>
                          ) : (
                            'Update'
                          )}
                        </Button>
                        <Button
                          type="button"
                          color="primary"
                          className="me-2"
                          aria-expanded="false"
                          onClick={() => setActiveStep(2)}
                        >
                          Selanjutnya
                        </Button>
                      </>
                    ) : (
                      <Button
                        type="submit"
                        color="primary"
                        className="me-2"
                        aria-expanded="false"
                        disabled={isSending}
                      >
                        {isSending ? (
                          <div className="d-flex align-items-center gap-2">
                            <Spinner size="sm" />
                            Mengirim..
                          </div>
                        ) : (
                          'Selanjutnya'
                        )}
                      </Button>
                    )}
                  </FormGroup>
                </Form>
              </CardBody>
            )}
          </Card>
        </Col>
      </Row>
    </>
  );
};

General.propTypes = {
  setActiveStep: PropTypes.func,
  generalIsDone: PropTypes.bool,
  setGeneralIsDone: PropTypes.func,
};

export default General;
