import React, { useState } from 'react';
import PropTypes from 'prop-types';
import {
  Button,
  Card,
  CardBody,
  Col,
  Form,
  FormGroup,
  Input,
  Row,
  Spinner,
  Table,
} from 'reactstrap';
import MaterialIcon from '@material/react-material-icon';
import { useQuery } from '@tanstack/react-query';
import getBase64 from '../../../utils/generateFile';
import useAxios from '../../../hooks/useAxios';
import { alert } from '../../../components/toast/Toast';

function years() {
  const currentDate = new Date();
  const thisYear = currentDate.getFullYear();
  const yearArray = [];
  for (let i = 2000; i <= thisYear; i++) {
    yearArray.push(i);
  }
  return yearArray;
}

const Portfolio = ({
  setActiveStep,
  portofolioIsDone,
  setPortofolioIsDone,
  setSuccessMsg,
  setErrMsg,
}) => {
  const [isOpenForm, setIsOpenForm] = useState();
  const [values, setValues] = useState({});
  const [file, setFile] = useState();
  const [isSending, setIsSending] = useState(false);
  const [isDeleting, setIsDeleting] = useState(false);
  const [selectedId, setSelectedId] = useState(undefined);

  const api = useAxios();

  const { isLoading, error, data, refetch } = useQuery({
    queryKey: ['porto'],
    queryFn: () =>
      api.get(`vapi/porto/my`).then((res) => {
        if (res.data.data.length > 0) {
          setPortofolioIsDone(true);
        } else {
          setPortofolioIsDone(false);
        }
        return res.data.data;
      }),
  });

  const handleChange = (e) => {
    setValues((prev) => ({ ...prev, [e.target.id]: e.target.value }));
  };

  const handleFile = (e) => {
    getBase64(e.target.files[0])
      .then((res) => setFile(res.split(',')[1]))
      .catch((err) => console.log(err));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setIsSending(true);
    values.file = file;
    await api
      .post('vapi/porto/store', values)
      .then((res) => {
        if (res.data.success === true) {
          refetch();
          alert('success', 'Portofolio berhasil disimpan');
          setSuccessMsg('Portofolio berhasil ditambahkan.');
        } else {
          alert('error', 'Portofolio gagal disimpan');
          setErrMsg('Portofolio gagal disimpan. Silakan coba lagi.');
        }
      })
      .catch(() => setErrMsg('Something went wrong.'));
    setIsOpenForm(false);
    setIsSending(false);
  };
  const handleDelete = async (id) => {
    setSelectedId(id);
    setIsDeleting(true);
    await api
      .get(`vapi/porto/delete/${id}`)
      .then((res) => {
        if (res.data.success === true) {
          refetch();
          setSuccessMsg('Portofolio berhasil dihapus.');
        } else {
          setErrMsg('Data portofolio gagal dihapus. Silakan coba lagi.');
        }
      })
      .catch(() => setErrMsg('Data portofolio gagal dihapus. Silakan coba lagi.'));
    setIsDeleting(false);
  };

  return (
    <Row lg="12" className="justify-content-center">
      <Col lg="12">
        <Card className="rounded-4 w-100">
          <CardBody>
            <h4 className="fw-bold">Portofolio Pengalaman</h4>
            {isLoading ? (
              'Loading...'
            ) : error ? (
              'Something went wrong.'
            ) : data?.length > 0 ? (
              <Table hover>
                <tbody>
                  {data.map((item, i) => (
                    <tr key={item.id_porto}>
                      <td>{i + 1}</td>
                      <td key={item.id_porto} style={{ textAlign: 'left' }}>
                        {item.nama_project}
                      </td>
                      <td>{item.tahun_mulai}</td>
                      <td>{item.tahun_selesai}</td>
                      <td>{item.durasi}</td>
                      <td>{item.nilai_po}</td>
                      <td style={{ textAlign: 'left' }}>{item.owner}</td>
                      <td style={{ textAlign: 'right', width: '100px' }}>
                        {isDeleting && selectedId === item.id_porto ? (
                          <span className="text-danger">Deleting..</span>
                        ) : (
                          <MaterialIcon
                            icon="delete"
                            style={{ fontSize: '16px', cursor: 'pointer', color: 'red' }}
                            onClick={() => handleDelete(item.id_porto)}
                          />
                        )}
                      </td>
                    </tr>
                  ))}
                </tbody>
              </Table>
            ) : (
              <div className="d-flex justify-content-center">Belum ada data.</div>
            )}
            <div className="d-flex justify-content-end mt-2">
              {isOpenForm ? (
                <Button
                  type="button"
                  color="dark"
                  size="sm"
                  outline
                  onClick={() => setIsOpenForm(false)}
                  content="center"
                >
                  Tutup
                </Button>
              ) : (
                <Button
                  type="button"
                  color="dark"
                  size="sm"
                  outline
                  onClick={() => setIsOpenForm(true)}
                >
                  Tambah
                </Button>
              )}
            </div>
            {isOpenForm && (
              <Form onSubmit={handleSubmit} className="mt-3">
                <Col className="bg-light-primary rounded-4 px-3 pt-3">
                  <Row>
                    <Col sm="12" lg="5">
                      <FormGroup>
                        <Input
                          type="text"
                          id="nama_project"
                          name="nama_project"
                          placeholder="Nama Project"
                          onChange={handleChange}
                        />
                      </FormGroup>
                    </Col>
                    <Col sm="12" lg="2">
                      <FormGroup>
                        <Input
                          type="select"
                          id="tahun_mulai"
                          name="tahun_mulai"
                          placeholder="Tahun Mulai"
                          onChange={handleChange}
                          defaultValue=""
                        >
                          <option value="" disabled>
                            - Tahun Mulai -
                          </option>
                          {years()
                            .reverse()
                            .map((y) => (
                              <option value={y} key={y}>
                                {y}
                              </option>
                            ))}
                        </Input>
                      </FormGroup>
                    </Col>
                    <Col sm="12" lg="2">
                      <FormGroup>
                        <Input
                          type="select"
                          id="tahun_selesai"
                          name="tahun_selesai"
                          placeholder="Tahun Berakhir"
                          onChange={handleChange}
                          defaultValue=""
                        >
                          <option value="" disabled>
                            - Tahun Selesai -
                          </option>
                          {years()
                            .reverse()
                            .map((y) => (
                              <option key={y} value={y}>
                                {y}
                              </option>
                            ))}
                        </Input>
                      </FormGroup>
                    </Col>
                    <Col sm="12" lg="3">
                      <FormGroup className="position-relative">
                        <Input
                          type="file"
                          id="fileSpk"
                          name="fileSpk"
                          onChange={handleFile}
                          accept="application/pdf"
                        />
                        <small className="text-muted position-absolute">
                          Upload file SPK (max. 10mb)
                        </small>
                      </FormGroup>
                    </Col>
                  </Row>
                  <Row className="mt-3">
                    <Col sm="12" lg="5">
                      <Row>
                        <Col sm="12" lg="6">
                          <FormGroup>
                            <Input
                              type="number"
                              id="durasi"
                              name="durasi"
                              placeholder="Durasi (ex. 32 Bulan)"
                              onChange={handleChange}
                            />
                          </FormGroup>
                        </Col>
                        <Col sm="12" lg="6">
                          <FormGroup>
                            <Input
                              type="number"
                              id="nilai_po"
                              name="nilai_po"
                              placeholder="Nilai Pekerjaan"
                              onChange={handleChange}
                            />
                          </FormGroup>
                        </Col>
                      </Row>
                    </Col>
                    <Col sm="12" lg="7">
                      <Row>
                        <Col sm="12" lg="12">
                          <FormGroup>
                            <Input
                              type="text"
                              id="owner"
                              name="owner"
                              placeholder="Pemilik Pekerjaan"
                              onChange={handleChange}
                            />
                          </FormGroup>
                        </Col>
                      </Row>
                    </Col>
                    <Col sm="12" lg="2" className="mb-2">
                      <Button type="submit" color="dark">
                        {isSending ? (
                          <div className="d-flex align-items-center gap-2">
                            <Spinner size="sm" />
                            Menyimpan..
                          </div>
                        ) : (
                          'Tambah'
                        )}
                      </Button>
                    </Col>
                  </Row>
                </Col>
              </Form>
            )}
            <FormGroup className="d-flex justify-content-end mt-3">
              <Button
                type="button"
                color="primary"
                className="me-2"
                outline
                onClick={() => setActiveStep(3)}
              >
                Sebelumnya
              </Button>
              <Button
                type="button"
                color="primary"
                className="me-2"
                onClick={() => setActiveStep(5)}
                disabled={!portofolioIsDone}
              >
                Selanjutnya
              </Button>
            </FormGroup>
          </CardBody>
        </Card>
      </Col>
    </Row>
  );
};

Portfolio.propTypes = {
  setActiveStep: PropTypes.func,
  portofolioIsDone: PropTypes.bool,
  setPortofolioIsDone: PropTypes.func,
  setSuccessMsg: PropTypes.func,
  setErrMsg: PropTypes.func,
};

export default Portfolio;
